/* BetPredictor App Styles */

/* Remove default centering */
.App {
  text-align: left;
  min-height: 100vh;
  background-color: #09090B;
}

/* Layout */
.main-layout {
  display: flex;
  min-height: 100vh;
}

.sidebar {
  width: 240px;
  background-color: #18181B;
  border-right: 1px solid #27272A;
  position: fixed;
  height: 100vh;
  overflow-y: auto;
}

.main-content {
  flex: 1;
  margin-left: 240px;
  padding: 24px;
  min-height: 100vh;
}

/* Navigation */
.nav-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  color: #A1A1AA;
  text-decoration: none;
  transition: all 0.15s ease;
  border-left: 2px solid transparent;
}

.nav-item:hover {
  background-color: #27272A;
  color: #FAFAFA;
}

.nav-item.active {
  background-color: rgba(204, 255, 0, 0.05);
  color: #CCFF00;
  border-left-color: #CCFF00;
}

/* Cards */
.stat-card {
  background-color: #18181B;
  border: 1px solid #27272A;
  border-radius: 12px;
  padding: 20px;
}

.event-card {
  background-color: #18181B;
  border: 1px solid #27272A;
  border-radius: 12px;
  padding: 16px;
  transition: all 0.15s ease;
}

.event-card:hover {
  border-color: #3F3F46;
  transform: translateY(-2px);
}

/* Buttons */
.btn-primary {
  background-color: #CCFF00;
  color: #09090B;
  font-weight: 700;
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition: all 0.15s ease;
}

.btn-primary:hover {
  background-color: #B3E600;
}

.btn-outline {
  background-color: transparent;
  color: #A1A1AA;
  font-weight: 600;
  padding: 10px 20px;
  border-radius: 8px;
  border: 1px solid #3F3F46;
  cursor: pointer;
  transition: all 0.15s ease;
}

.btn-outline:hover {
  background-color: #27272A;
  color: #FAFAFA;
}

/* Odds display */
.odds-value {
  font-family: 'JetBrains Mono', monospace;
  font-weight: 700;
}

.odds-value.positive {
  color: #22C55E;
}

.odds-value.negative {
  color: #EF4444;
}

/* Responsive */
@media (max-width: 1024px) {
  .sidebar {
    width: 200px;
  }
  
  .main-content {
    margin-left: 200px;
    padding: 16px;
  }
}

@media (max-width: 768px) {
  .sidebar {
    display: none;
  }
  
  .main-content {
    margin-left: 0;
  }
  
  .mobile-nav {
    display: flex;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #18181B;
    border-top: 1px solid #27272A;
    z-index: 50;
  }
}

/* Custom Scrollbar for Analysis Sections */
.custom-scrollbar::-webkit-scrollbar {
  width: 4px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: transparent;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background-color: #3F3F46;
  border-radius: 2px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background-color: #52525B;
}

/* Analysis Section Animations */
.analysis-section {
  transition: all 0.2s ease-in-out;
}

.analysis-section-header {
  cursor: pointer;
  user-select: none;
}

.analysis-section-content {
  overflow: hidden;
  transition: max-height 0.3s ease-in-out;
}

/* Line clamp utility */
.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.line-clamp-3 {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

